import React from 'react';
import ReactDOM from 'react-dom';
// inporting css
import './Stones.css';

function Stones(props){
    return(
        <div className='stones' style={props.stoneOfStyle} onClick={
            ()=>props.function(props.obj)
        }>
            {props.numerator}
            <hr style={{ margin: '6px' }} />
            {props.denominator}
        </div>
    );
}

export default Stones;